import React from 'react'
import { StaticImage } from 'gatsby-plugin-image'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage } from 'gatsby-plugin-image'
import { BgImage } from "gbimage-bridge"
import styled from '@emotion/styled'

import IntroModule from '../../../components/IntroModule'

import plan_mobile_bg from './plan_mobile_bg.png'


const GatsbyBgImage = styled(BgImage)`
height: 100%;
width: 100%;
`

const IntroPlan = ({ fullpageApi, isActive, onLoad = () => {} }) => {
  const { desk } = useStaticQuery(
    graphql`
      query planquery {
        desk: file(relativePath: { eq: "plan_bg.png" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              width: 1920
            )
          }
        }
      }
    `
  )
  const imageDesk = getImage(desk)
  return (
    <IntroModule
      cover={<GatsbyBgImage image={imageDesk} onLoad={onLoad} />}
      isActive={isActive}
      mobileCover={plan_mobile_bg}
      fullpageApi={fullpageApi}
      title={`\n`}
      text={`19972009`}
    />
  )
}

export default IntroPlan
