import React from 'react'
import styled from '@emotion/styled'
import { BgImage } from 'gbimage-bridge'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage } from 'gatsby-plugin-image'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Button from '../../../components/Button'
import { responsive } from '../../../contexts/responsive'



const GatsbyBgImage = styled(BgImage)`
height: 100%;
width: 100%;
`

const Care = ({ fullpageApi, onLoad = () => {} }) => {
  const { desk } = useStaticQuery(
    graphql`
      query CareQuery {
        desk: file(relativePath: { eq: "data_bg.png" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              width: 1920
            )
          }
        }
      }
    `
  )
  const imageDesk = getImage(desk)
  return (
    <Box className="section">
      <Box.Absolute height="100%" greaterThanOrEqual="md" top="0" left="0" right="0">
        <GatsbyBgImage image={imageDesk} onLoad={onLoad} />
      </Box.Absolute>
      <Box.Relative color="white" mx={responsive('auto', 'unset')} px={responsive('1em', '8.875em')} maxWidth={responsive('20.75em', '64em')}>
        <Text fontWeight={'black'} letterSpacing={responsive('0.25em', '0.5rem')} fontSize={responsive('1.875em', '3.75em')} mr={responsive(0, '3em')}></Text>
        <Text mt={responsive('0.75rem', '0.33em')} fontSize={responsive('1em', '1.625em')}>
          <br />
          <br />
          
        </Text>
        <Box mt="1.5em">
          <Button.Outline
            fontSize={responsive('1em', '2.25em')}
            px={responsive('0.875em', '1em')}
            py={responsive('0.5em', '0.4em')}
            onClick={() => fullpageApi.moveSectionDown()}
          ></Button.Outline>
        </Box>
      </Box.Relative>
      <Box.Absolute bottom="1em" right={responsive('1em', '2em')} color="white"></Box.Absolute>
    </Box>
  )
}

export default Care
