import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage } from 'gatsby-plugin-image'
import { BgImage } from "gbimage-bridge"
import styled from '@emotion/styled'

import IntroModule from '../../../components/IntroModule'

import bg_far_mobile from './bg_far_mobile.png'

const GatsbyBgImage = styled(BgImage)`
height: 100%;
width: 100%;
`

const Intro = ({ fullpageApi, isActive, onLoad = () => {} }) => {
  const { desk } = useStaticQuery(
    graphql`
      query introquery {
        desk: file(relativePath: { eq: "bg_far.png" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              width: 1920
            )
          }
        }
      }
    `
  )
  const imageDesk = getImage(desk)
  return (
    <IntroModule
      isActive={isActive}
      cover={<GatsbyBgImage image={imageDesk} onLoad={onLoad} />}
      mobileCover={bg_far_mobile}
      fullpageApi={fullpageApi}
      title=""
      text={`\n`}
    />
  )
}

export default Intro
